'use client'

import useglobalStore from "@/context/GlobalStore";
import { useRouter } from "next/navigation";
import React, { useEffect, useRef } from "react";


const Games = () =>
{
    const manager = useglobalStore();
    const gameSocketRef = useRef<WebSocket | null>(manager.gameSocket);
    useEffect(() =>
    {
        gameSocketRef.current = manager.gameSocket;
    }, [manager.gameSocket])

    useEffect(() =>
    {
        if (gameSocketRef.current)
            return ;
        console.log("creating game socket....");
        gameSocketRef.current = new WebSocket("ws://localhost:4000/games");
        manager.updateGameSocket(gameSocketRef.current);
        gameSocketRef.current.onclose = () =>
        {
            console.log("closing game socket...");
            gameSocketRef.current?.close();
            manager.updateGameSocket(null);
        }

        gameSocketRef.current.onmessage = (msg) =>
        {
            const data = JSON.parse(msg.data.toString());
            console.log("GAME: ", data);
        }

    }, [])

    const router = useRouter();
    return (
        <>
            <div className="h-screen w-screen flex items-center justify-center gap-x-10 ">
                <div onClick={()=>{
                    router.push('/games/local');
                    const data = {type: "local"};
                    gameSocketRef.current?.send(JSON.stringify(data));

                }} className="size-40 bg-green-300 text-[20px] flex items-center justify-center">Local</div>
                <div className="size-40 bg-green-300 text-[20px] flex items-center justify-center">Tournament</div>
                <div className="size-40 bg-green-300 text-[20px] flex items-center justify-center">TTT section</div>
            </div>
        </>
    )
}

export default Games;